<template><section><html><head></head><body><h1>MapboxStyleImageryProvider</h1>
<p><code v-pre="">vc-provider-imagery-style-mapbox</code> 组件用于加载由 Mapbox 托管的自定义影像服务图层。</p>
<h2>示例</h2>
<h3>加载 Mapbox 自定义风格影像服务图层</h3>
