<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td><code v-pre="">'https://api.mapbox.com/style/v1/'</code></td>
<td><code v-pre="">optional</code>指定 Mapbox 服务地址。</td>
</tr>
<tr>
<td>username</td>
<td>String</td>
<td><code v-pre="">'mapbox'</code></td>
<td><code v-pre="">optional</code> 指定加载的 mapbox 服务用户名。</td>
</tr>
<tr>
<td>styleId</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定加载的 mapbox 服务的 StyleID。</td>
</tr>
<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定加载的 mapbox 服务秘钥。</td>
</tr>
<tr>
<td>tilesize</td>
<td>Number</td>
<td><code v-pre="">512</code></td>
<td><code v-pre="">optional</code> 指定加载的 mapbox 服务的瓦片大小。</td>
</tr>
<tr>
<td>scaleFactor</td>
<td>Boolean</td>
<td></td>
<td><code v-pre="">optional</code> 指定是否以 @2x 比例渲染。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>参考椭球体，没指定的话默认 WGS84。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>最大层级。</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>图层的矩形范围,此矩形限制了影像可见范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>credit</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>服务描述信息。</td>
</tr>
</tbody>
</table>
